*, *::before, *::after {
  box-sizing: border-box;
}

ul, ol, li {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

dl, dd {
  margin: 0;
}

.c-bg--green {
  background-color: green;
}

.c-bg--red {
  background-color: red;
}

.c-bg--gray {
  background-color: gray;
}

.c-bg--beige {
  background-color: beige;
}

.c-bg--white {
  background-color: white;
}

.c-image {
  vertical-align: bottom;
  width: 100%;
}

.c-oval {
  border-radius: 25%;
}

.c-square {
  border-radius: 5%;
}